import React, { Component } from 'react'
import Button from 'ic-button'
import Markdown from 'ic-markdown'
import code from './index.md'
class App extends Component {
  render() {
    return (
      <section>
        <section>
          <Button icon='search_normal' size='large' type='primary'>  </Button>{' '}
          <Button size='large' type='primary'></Button>{' '}
          <Button size='large'></Button>{' '}
          <Button size='large'></Button>{' '}
          <Button size='large' icon='search_normal' disabled ></Button>{' '}
        </section>
        <section>
          <Button type='primary' icon='search_normal'></Button>{' '}
          <Button type='primary'></Button>{' '}
          <Button></Button>{' '}
          <Button></Button>{' '}
          <Button disabled></Button>{' '}
        </section>
        <section>
          <Button type='primary' size='small' icon='search_normal'></Button>{' '}
          <Button type='primary' size='small'></Button>{' '}
          <Button size='small'></Button>{' '}
          <Button size='small'></Button>{' '}
          <Button size='small' disabled></Button>{' '}
        </section>
        <Markdown>{code}</Markdown>
      </section>
    )
  }
}

export default App



